.FormSms{
    height: 10vh;   
    width: 80vw;
    margin-top: 2vh;
    margin-bottom: 9vh;
    background-color: lavender;
    border: solid lightgray 1px;
    display: flex; 
    border-radius: 15px;
}
.FormSms__message{
    font-family: verdana;
    padding: 1%;
    margin: auto;
    font-size: 13px;
}
.FormSms__select{
    background-color: lightskyblue;
    border: solid lightslategray .5px;
    font-weight: 700;
    margin: 0vh 1vw;
}
.FormSms__number{
    width: 7vw;
    margin: 0px 8px;
    font-weight: 700;
    background-color: lightskyblue;
    border: solid lightslategray .5px;
}
.FormSms__button{
    color: ivory;
    font-family: 'Grandstander', cursive;
    background-color: #060930;
    border-radius: 15px;
    padding: 1vh 2vw;
    border: none; 
    /* margin-left:5vw; */
}
.FormSms__button:hover{
    /* color: black; */
    background-color: lightslategrey;
}