.Header{
    height: 15vh;
    display: flex;
    align-items: center;
    
}
.Header__logo{
    border-radius: 100%;
    width: 50px;
    height: 50px;
    margin-left: 1vw;
    margin-right: 2vw;
}
.Header__item{
    font-family: 'Grandstander', cursive;
    font-size: 20px;
    color: black;
    text-decoration: none;
    font-weight: 600;
}
.Nav{
    background-color: #14274e;
    height:8vh;
    display: flex;
    justify-content: start;
    align-items: center;
}
.Nav__item{
    font-family: sans-serif;
    font-size: 15px;
    margin-left: 10vw;
    text-decoration: none;
    font-weight: 700;
    color: ivory;
}
.Nav__item:hover{
   color: dimgray; 
}
.Nav__container{
    display: flex;
}