.Button{
    width: 7vw;
    height: 5vh;
    border: none;
    color:white;    
    box-shadow: 0 2px 25px rgba(0, 183, 255, 0.5);
    font-weight: 700;
    font-size: 15px;
    font-family: 'Grandstander', cursive;
    background-color:darkblue;   
    border-radius: 100%;
    padding: 1px;
}
.Button:hover  {
    background-color: whitesmoke;
}
.Button__inactive{
    background-color: gray;
    
}
.Button__inactive:hover{
    background-color: gray;
    
}